.browse-card {
  height: 100%;
  background-color: rgb(106, 103, 99);
  padding: 0px;
}
div.card.browse-card{
 height:20em;
 width:14em;
  
}
#root > div > div > div:nth-child(3) > div > img{
  height:5em;
  width:5em;
  margin:auto;
}
.challenge-title {
  font-size: 2em;
  color: whitesmoke;
}
.challenge-text {
  color: whitesmoke;
  font-size: 1em;
}

p.card-text.challenge-info {
  background-color: white;
  padding: 2%;
  height:2em;
  font-size:0.8em;
}
.fa-share-alt {
  float: right;
}
.card-body {
  text-align: center;
  
}

.card {
  background: rgb(108, 108, 108);
  padding: 2px;
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  width: 30%;
  height: 50%;
  box-shadow: 0 0.5px 2px #999, 0 1px 1px #999;
  text-align: left;
}
body {
  background-color: rgb(67, 66, 66);
  margin-right:0px;
  padding-right:0px;
}

.card > .img-container {
  height: 100%;
  width: 30%;
  float: left;
  text-align: center;
}
.card > .img-container > h4 {
  color: white;
}

.card > .img-container > img {
  width: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}
#root > div > div > div > div > div > p{
  font-size: 0.8em;
}
#root > div > div > div > div > div > a > h5{
  font-size:1em;
}
@media screen and (max-width:640px){
  #root > div > div > div:nth-child(3) > div{
    margin:auto;
    margin-top:1em;
    z-index:-2;
  }
  #root > div > div > div:nth-child(3) > div:nth-child(1){
    margin:auto;
    margin-top:8em;
    
  }
}